/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* — you can add your own custom styles below — */
@layer utilities {
    /* If you want to give a named utility instead of typing animate-rotate-slow */
    .animate-rotate-slow { animation: rotate360 4s linear infinite; }
    .animate-bounce-slow { animation: bounceY 2s ease-in-out infinite; }
  }